import Slider from "react-slick";
import "../css/offert.css";

function Offert() {
  var settings = {
    dots: true,
    infinite: false,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div className="offert" id="Offert">
      <div className="slider-container">
        <Slider {...settings}>
          <div className="item">
            <p className="title mt-4 ms-4">Trening odchudzajcy</p>
            <div className="text-end mt-4 me-3 item-body">
              <p>Trening cardio (wytrzymaociowy)</p>
              <p>Trening interwaowy o wysokiej intensywnoci (HIIT)</p>
              <p>Trening siowy</p>
              <p>wiczenia funkcjonalne</p>
              <p>Monitorowanie postpw i modyfikacja planu treningowego</p>
            </div>
          </div>
          <div className="item ">
            <p className="title mt-4 ms-4">Trening Sylwetkowy</p>
            <div className="text-end mt-4 me-3 item-body">
              <p>Trening kardio (aerobowy)</p>
              <p>Trening siowy</p>
              <p>Trening interwaowy o wysokiej intensywnoci (HIIT)</p>
              <p>Trening mobilnoci i rozcigania</p>
              <p>Trening core i stabilizacji</p>
            </div>
          </div>
          <div className="item">
            <p className="title mt-4 ms-4">Trening wytrzymaociowy </p>
            <div className="text-end mt-4 me-3 item-body">
              <p>Trening interwaowy o niskiej intensywnoci (LISS)</p>
              <p>Trening interwaowy o wysokiej intensywnoci (HIIT)</p>
              <p>Trening siowy wytrzymaociowy</p>
              <p>Trening obwodowy (circuit training)</p>
              <p>Long Steady State - LSS</p>
            </div>
          </div>
          <div className="item">
            <p className="title mt-4 ms-4">trening siowy </p>
            <div className="text-end mt-4 me-3 item-body">
              <p>wiczenia wielostawowe (zoone)</p>
              <p>wiczenia izolowane (jednostawowe)</p>
              <p>Zasady progresji i zwikszania obcienia</p>
              <p>Techniki treningowe i programowanie</p>
              <p>Rozgrzewka i cool-down</p>
            </div>
          </div>
          <div className="item">
            <p className="title mt-4 ms-4">Trening funkcjonalny</p>
            <div className="text-end mt-4 me-3 item-body">
              <p>wiczenia wielostawowe</p>
              <p>wiczenia z wasn mas ciaa</p>
              <p>wiczenia na rwnowag i stabilno</p>
              <p>Trening core (minie gbokie)</p>
              <p>wiczenia plyometryczne</p>
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
}

export default Offert;
